import React from 'react'
import Main from '../templates/Main'
import './index.css'
import imagem from './goku-vs-raditz.jpeg'
import gokuvsvega from './goku-vegeta.jpg'
import goku_vegeta from './goku-vs-vegeta.jpg'
import goku_ssj from './goku-ssj1.jpg'
import goku_freeza from './goku-ssj-freeza.jpg'
import piccolo from './piccolo.jpg'
import trunks from './trunks-ssj1.jpg'
import androids from './androids.jpg'
import cellF1 from './cell.jpg'
import cellPerfect from './cell-perfect.jpg'
import gohanVScell from './gohan-vs-cell.jpg'
import sagaBoo from './saga-boo.jpg'
import gokuVSvegetaSsj from './goku-vs-vegeta-ssj.jpg'
import gokuSsj3MajinGordo from './gokussj3-vs-majinGordo.jpg'
import vegito from './vegito.jpg'
import superVegito from './superVegito.jpg'
import vegito2 from './vegito2.jpg'
import SagaBoo from './saga-boo.jpg'
import gokuSsj3VsKidoo from './goku-ssj3-vs-kidBoo.jpg'
import gokuGenki from './goku-genkdama.jpg'

export default props => {
    return (
        <Main className=''>
            
            <div className="conteudo">
                <div className="" id='conteudo'>
                    <h1>Historia do Dragon ball Z</h1>
                </div>
                <div className="mt-4">
                    <p>
                    Dragon Ball  uma franquia de mdia japonesa criada por <strong>Akira Toriyama</strong>. Originalmente iniciada com uma srie de mang que foi escrita e ilustrada por Toriyama, teve os seus captulos serializados na revista Weekly Shonen Jump de 1984 a 1995, com os seus 519 captulos compilados em 42 volumes e publicados pela editora Shueisha. Dragon Ball foi inicialmente inspirado pelo clssico romance chins Jornada ao Oeste. A srie segue as aventuras do protagonista, Son Goku, desde sua infncia at a idade adulta enquanto ele treina artes marciais e explora o mundo em busca de sete esferas conhecidas como as Esferas do Drago, que convocam um drago que concede um desejo quando reunidas. Ao longo de sua jornada, Goku faz vrios amigos e luta contra uma grande variedade de viles, muitos dos quais tambm procuram as Esferas do Drago.

                    </p>
                </div>
                <div className="mt-7">
                    <h4 className="mt-6">Sagas Dragon Ball Z</h4>
                    <p className="mt-2">
                        Dragon Ball Z possui quadro sagas. sendo elas, saiyajins, freeza, cell e majin boo.
                        Vo pode aconpanhar um breve resumo a seguir...
                    </p>
                </div>
                <div className=" mt-7 saga">
                    <h4 className="mt-5">Saga Saiyajin</h4>
                    <hr/>
                    <p className='p-2'>
                    Depois dos eventos de Dragon Ball, 
                    Goku viaja at  ilha do Mestre Kame para apresentar o seu primeiro filho, 
                    Gohan, aos seus amigos. 
                    Entretanto o reencontro  interrompido com a chegada de um misterioso guerreiro aliengena chamado Raditz, 
                    que revela que Goku  tambm um extraterrestre oriundo do planeta Vegeta e que ele  na realidade seu irmo.                       
                    </p>
                    <p>
                    Raditz diz ainda que est na terra para matar todos os humanos, 
                    rapta Gohan e desaparece. Goku e Piccolo o vo procurar e iniciam um combate intenso contra Raditz. Durante a batalha Gohan consegue machucar Raditz, algo que permite a Piccolo lanar um ataque que acaba por matar o vilo e Goku. Antes de morrer Raditz revela que no prazo de um ano, 
                    mais dois saiyajins bem mais poderosos do que ele chegariam  Terra
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={imagem} alt='imagem goku vs raditz'/>
                    </div>
                    <p>
                    Piccolo comea assim a treinar Gohan, os restantes Guerreiros Z iniciam os seus treinos com a ajuda do Kami-Sama e Goku, 
                    agora morto, fica treinando no planeta do Senhor Kaio.
                    </p>
                            
                    <p className=''>
                    Passado um ano os saiyajins Vegeta e Nappa chegam na Terra e matam todos os Guerreiros Z com a exceo de Kuririn e Gohan. 
                    Goku  ressuscitado pelo Mestre Kame atravs das Esferas do Drago.   
                    </p>
                    <p>
                    Goku vence Nappa (que acaba morrendo s mos de Vegeta). Kuririn que empunhava a espada Yajirobe acaba por poupar a vida do Princpe dos Saiyajins a pedido de Goku.
                    </p>
                    <div className="conte-flex">
                        < img className="m-3 p-2" src={piccolo} alt='piccolo'/>
                        < img className="m-3 p-2" src={gokuvsvega}/>
                        < img className="m-3 p-2" src={goku_vegeta}/>
                    </div>
                            
                </div>
                <div className=" mt-4 sagafree">
                    <h4 className="mt-4">Saga Freeza</h4>
                    <hr/>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={goku_ssj}/>
                    </div>
                    <p className='p-2'>
                    Gohan, Kuririn e Bulma iniciam uma misso para encontrar o Planeta Namekusei - de onde so oriundos Piccolo e Kami-Sama - eles pretendem usar as Esferas do Drago deste planeta para serem capazes de ressuscitar todos os Guerreiros Z. Contudo quando eles chegam l, encontram uma nova ameaa chamada Freeza, que juntamente com o seu exercito esto igualmente procurando as Esferas do Drago.
                    </p>
                    <p className='p-2'>
                    Freeza mata grande parte da populao de Namekusei, enquanto que Vegeta tambm est no planeta causando imensos estragos para os seus habitantes. Depois de recuperar de vrias leses, Goku comea a sua viagem para Namekusei de forma a salvar todo o mundo.
                    </p>
                    <p className='p-2'>
                    Em desespero Vegeta se alia a Gohan e Kuririn numa tentativa de derrotar Freeza e o seu exrcito, contudo eles so derrotados pelos tropas do vilo. Goku chega ao planeta e consegue eliminar grande parte dos aliados de Freeza, mas o seu corpo acaba sendo roubado pelo Capito Ginyu.
                    </p>
                    <p className='p-2'>
                    Enquanto que Goku recupera das leses causadas pelos Guerreiros Z no processo de recuperarem o seu corpo, Freeza busca por algum que lhe explique como as Esferas do Drago funcionam. Isto faz com que Gohan e Kuririn consigam utilizar as Esferas do Drago para ressuscitar Piccolo - no final o criador das Esferas do Drago morre as tornando inutilizveis.
                    </p>
                    <p className='p-2'>
                    Freeza evolui para a sua forma final e derrota Piccolo e Vegeta com grande facilidade. Goku entra em cena e v o vilo matar Vegeta, deixando Piccolo gravemente ferido e acaba tambm matando Kuririn, isto faz com que Goku fique furioso e se transforme pela primeira vez em Super Saiyajin.
                    </p>
                    <p className='p-2'>
                    Sabendo que no pode ganhar, Freeza destri o Planeta Namekusei, mas os seus habitantes so ressuscitados pelas Esferas do Drago e os restantes Guerreiros Z pelo Drago Porunga. Tanto Goku como Freeza sobrevivem  exploso do planeta.
                    </p>
                    <div className="conte-flex">
                    <img className="m-3 p-2" src={goku_freeza}/>
                    </div>

                </div>
                <div className="mt-4">
                    <h4>
                        Saga androids / cell
                    </h4>
                    <hr/>
                    <p>
                    Um ano depois dos acontecimentos do Planeta Namekusei, Freeza (agora recuperado) viaja para o Planeta Terra na companhia do seu pai. 
                    Sem saber do paradeiro de Goku,
                     os Guerreiros Z so salvos por um misterioso Super Saiyajin que consegue derrotar Freeza.
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={trunks} alt="trunks"/>
                    </div>
                    <p>
                    Assim que Goku retorna, eles ficam sabendo que o misterioso Super Saiyajin  na realidade o filho de Vegeta e Bulma - Trunks - que veio do futuro para avisar Goku que ele iria contrair um vrus muito perigoso para o seu corao. 
                    Para alm disso ele avisou que em trs anos o planeta seria atacado por androides.
                    </p>
                    <p>
                    Passados trs anos, os Guerreiros Z so atacados pelos androides e Goku fica infectado pelo vrus, felizmente todos so salvos por Vegeta que agora consegue se transformar em Super Saiyajin. 
                    Um dos androides escapa e acaba libertando outros trs que derrotam facilmente os Guerreiros Z.
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={androids} alt="androids"/>
                    </div>
                    <p>
                    Entretanto, uma nova criatura chamada Cell entra em ao tendo apenas uma misso - consumir dois androides para atingir a sua forma perfeita e destruir o planeta.
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={cellF1} alt="cell"/>
                    </div>                  
                    <p>
                    Mal recupera do vrus, Goku leva Gohan, Vegeta e Trunks para a Sala do Tempo Sagrado (onde 1 ano equivale a 1 dia passado a terra) para que possam treinar e aumentar o seu nvel de poder. Durante esse perodo Cell consegue ingerir um dos androides (o N 17), atingindo assim a sua forma semi-perfeita. Trunks e Vegeta acabam o seu treino na Sala do Tempo e conseguem derrotar Cell facilmente, 
                    contudo o vilo joga com o orgulho de Vegeta e diz que ele no o conseguiria derrotar na sua forma perfeita.
                    </p>
                    <p>
                    Vegeta deixa que Cell se transforme na sua forma perfeita e o antagonista acaba mesmo por derrotar o Saiyajin. Surpreendido com o aumento do poder dos seus adversrios num espao de tempo to curto, 
                    Cell decide realizar um torneio de artes marciais.
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={cellPerfect} alt="cell"/>
                    </div>
                    <p>
                    O primeiro a enfrentar Cell no torneio  o campeo de artes marciais, Mr. Satan, mas ele  rapidamente eliminado. Logo a seguir foi a vez de Goku, contudo ele desiste depois de uma batalha bem intensa. Gohan foi o prximo a participar no torneio, demonstrando um nvel de poder surpreendente, conseguindo atingir uma nova forma, 
                    a de Super Saiyajin 2 e retirando um dos androides do corpo de Cell.
                    </p>
                    <p>
                    Cell ameaa destruir o planeta atravs de uma tcnica de auto-destruio, mas Goku impede que isso acontea o teletransportando para o planeta do Senhor Kaio e morrendo no processo. Cell consegue sobreviver e retorna para tentar um ltimo golpe, 
                    contudo Gohan se mostra superior ao vilo lanando um ataque que o acaba por destruir.
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={gohanVScell} alt="gohan vs cell"/>
                    </div>
                </div>
                <div>
                    <h4>Saga Majin Boo</h4>
                    <hr/>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={sagaBoo} alt="saga boo"/>
                    </div>
                    <p>
                    Passados sete anos do Torneio do Cell, o Kuririn  agora casado com a Androide 18 e Gohan se apaixonou pela filha do Mr. Satan - Videl - que o convence a entrar no Torneio de Artes Marciais. Todos os Guerreiros Z decidem participar no Torneio e Goku se junta a eles, depois de ter sido autorizado a vir para o reino dos vivos apenas por um dia.
                    </p>
                    <p>
                    Durante o torneio, Gohan conhece o Kaio Supremo, que o avisa que um feiticeiro colocou um monstro terrvel no Planeta Terra h milhes de anos e que agora o seu filho tinha chegado para ressuscitar o monstro e matar toda a vida do universo.
                    </p>
                    <p>
                    Vegeta perde o controle durante a sua batalha contra Goku no torneio e liberta uma quantidade de poder suficiente para que o monstro Majin Boo possa voltar  vida.
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={gokuVSvegetaSsj} alt="majin vegeta vs goku"/>
                    </div>
                    <p>
                    Gohan viaja com o Kaio Supremo para o seu mundo para iniciar um treino especial, enquanto que Vegeta se auto-destri numa tentativa fracassada de derrotar Majin Boo. Goku  autorizado a sair provisoriamente do Reino dos Mortos para tentar destruir o vilo e para isso ele se transforma pela primeira vez em Super Saiyajin 3, contudo no consegue alcanar o seu objetivo. Depois de matar o seu mestre, o agora livre Majin Boo faz amizade com um pequeno cachorro e com o campeo de Artes Marciais, Mr. Satan.
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={gokuSsj3MajinGordo} alt="goku ssj3"/>
                    </div>
                    <p>
                    Esta nova amizade faz com que ele comece a sentir um conflito interno e acaba por se dividir em dois seres distintos, um bom e outro mau. Contudo o Majin Boo malvado, conseguiu ultrapassar o poder do Majin Boo, 
                    o absorvendo e criando assim o Super Boo.
                    </p>
                    <p>
                    Numa tentativa de derrotar o vilo, Trunks e Goten so ensinados por Piccolo a utilizar a Dana Metamoru, conseguindo assim se fundir num novo guerreiro, o poderoso Gotenks. Este novo personagem resultante da fuso consegue alguns feitos bem impressionantes como se transformar em Super Saiyajin 3, 
                    mas mesmo assim no foi capaz de derrotar o Super Boo.
                    </p>
                    <p>
                    Entretanto, Gohan retorna do seu treino na companhia de Goten e Trunks e os trs conseguem derrotar o Super Boo, mas isso no dura muito tempo, pois o vilo os consegue enganar e aproveita para os absorver. No meio de todos estes desenvolvimentos o Velho Kaiohshin d a sua vida para que Goku possa ressuscitar.
                    </p>
                    <p>
                    Goku e Vegeta (que  autorizado pelo Rei Yemma a sair temporariamente do Reino dos Mortos) utilizam a fuso do Vegito, apesar das facilidades em enfrentar o Super Boo, eles se deixam absorver pelo inimigo. Dentro do corpo do Super Boo, eles conseguem libertar todos os seus amigos assim como o Majin Boo original, 
                    obrigando o vilo a assumir a sua forma base - Kid Boo.
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={vegito} alt="vegito"/>
                        <img className="m-3 p-2" src={superVegito} alt="super vegito"/>
                        <img className="m-3 p-2" src={vegito2} alt="vegito"/>
                    </div>
                    <p>
                    O Kid Boo destri o Planeta Terra, enquanto que Goku consegue salvar Vegeta e o Mr. Satan os levando para o Planeta Sagrado de Kaiohshin, mas o vilo os persegue. Nesta nova localizao, enquanto foram pedidos os desejos s Esferas do Drago do Planeta Namek para ressuscitar todos aqueles que morreram fruto das consequncias de Kid Boo e para recuperar a Terra, Goku conseguiu finalmente derrotar o vilo, utilizando a tcnica do Genki Dama.
                    </p>
                    <div className="conte-flex">
                        <img className="m-3 p-2" src={gokuSsj3VsKidoo} alt="goku ssj3"/>
                        <img className="m-3 p-2" src={gokuGenki} alt="goku ssj3"/>
                        <img className="m-3 p-2" src={SagaBoo} alt="goku"/>
                    </div>
                    <h4 className="mt-4 center">E assim, se encerra a historia do anime mais famoso do mundo</h4>

                </div>
            </div>

        </Main>
    )
}